import { useRef, useState } from "react";

export function BrowserMoveButton({onDoubleClick, onMouseDown, ...props}) {
    const [clickCount, setClickCount] = useState(1)
    let timerId = useRef()

    const onClickHandler = () => {
        if (timerId)
            clearTimeout(timerId);

        setClickCount((prev) => prev + 1)
        if (clickCount === 1){
            timerId = setTimeout(() => setClickCount(1), 300)
        }
        if (clickCount >= 2){
            onDoubleClick()
            setClickCount(1)
        }
    }

    return (
    <button
        {...props}
        onMouseDown={() => {onMouseDown(); onClickHandler()}} 
    ></button>)
}
