import { useState, useRef } from "react";

export function MobileMoveButton({OnDoubleTouch, onTouchStart, ...props}) {
    const [touchCount, setTouchCount] = useState(1)
    let timerId = useRef()

    const onTouchHandler = () => {
        if (timerId)
            clearTimeout(timerId);

        setTouchCount((prev) => prev + 1)
        if (touchCount === 1){
            timerId = setTimeout(() => setTouchCount(1), 300)
        }
        if (touchCount >= 2){
            OnDoubleTouch()
            setTouchCount(1)
        }
    }

    return (
        <button 
            {...props}
            onTouchStart={() => {onTouchStart(); onTouchHandler()}} 
        ></button>
    )
}