import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import './Controller.css'
import {BrowserView, MobileView} from 'react-device-detect'; 
import { BrowserMoveButton } from './BrowserMoveButton';
import { MobileMoveButton } from './MobileMoveButton';


export function Controller({setRequest, socket})
{
    const navigate = useNavigate();
    const startMenuEvent = {"Role":"Assistant", "Type":"Quit", "ForServer":false};
    const moveEvent = {"Key":null, "State":null, "Role":"Assistant", "Type":"InputEvent", "ForServer":false}
    const [shiftState, setShiftState] = useState(false)
    const [interact, setInteract] = useState(false)
    const [left, setLeft] = useState(false)
    const [right, setRight] = useState(false)

    const A = 'A'
    const D = 'D'
    const W = 'W'
    const Shift = 'LeftShift'
    const Up = 'Up'
    const Down = 'Down'

    function handleExit()
    {
        socket.send(JSON.stringify(startMenuEvent)) //TODO
        navigate('/')
    }

    useEffect(() => {
        window.onblur = () =>{
            handleExit()
        }
    }, [])

    function sendEvent(key, code) {
        let event = {...moveEvent}
        event['Key'] = key
        event['State'] = code
        socket.send(JSON.stringify({...event})) //TODO
    }
    
    function shiftDown() {
        if(shiftState) return;
        setShiftState(true)
        sendEvent(Shift,Down)
    }
    
    function shiftUp() {
        if(!shiftState) return;
        setShiftState(false)
        sendEvent(Shift,Up)
    }
    
    function downInteract() {
        setInteract(true)
        sendEvent(W, Down)
    }

    function upInteract() {
        setInteract(false)
        sendEvent(W, Up)
    }

    function downLeft() {
        setLeft(true)
        sendEvent(A, Down)
    }

    function upLeft() {
        setLeft(false)
        sendEvent(A, Up)
    }

    function downRight() {
        setRight(true)
        sendEvent(D, Down)
    }

    function upRight() {
        setRight(false)
        sendEvent(D, Up)
    }

    return(
        <>
            <div className="joystick">
                <button className="exit" onClick={handleExit}>Выйти</button>
                <MobileView>
                    <div id="move-button-container">
                        <MobileMoveButton 
                            id={"left"}
                            OnDoubleTouch={shiftDown}
                            className={left ? "press-button" : "up-button"}
                            onTouchStart={() => {downLeft()}} 
                            onTouchEnd={() => {upLeft(); shiftUp()}}>
                        </MobileMoveButton>
                        <MobileMoveButton 
                            id={"right"}
                            OnDoubleTouch={shiftDown}
                            className={right ? "press-button" : "up-button"}
                            onTouchStart={() => {downRight()}} 
                            onTouchEnd={() => {upRight(); shiftUp()}}>
                        </MobileMoveButton>
                        <button id="interact"
                            className={interact ? "press-button" : "up-button"}
                            onTouchStart={downInteract} onTouchEnd={upInteract} 
                        ></button>
                    </div>
                </MobileView>
                <BrowserView>
                    <div id="move-button-container">
                        <BrowserMoveButton 
                            id={"left"} 
                            onDoubleClick={shiftDown}
                            onMouseDown={() => {downLeft()}}
                            onMouseUp={() => {upLeft(); shiftUp()}}
                            onMouseOut={upLeft}
                            className={left ? "press-button" : "up-button"}>
                        </BrowserMoveButton>
                        <BrowserMoveButton 
                            id={"right"} 
                            onDoubleClick={shiftDown}
                            onMouseDown={() => {downRight()}}
                            onMouseUp={() => {upRight(); shiftUp()}}
                            onMouseOut={upRight}
                            className={right ? "press-button" : "up-button"}>
                        </BrowserMoveButton>
                        <button id="interact"
                            className={interact ? "press-button" : "up-button"}
                            onMouseDown={downInteract} onMouseUp={upInteract} onMouseOut={upInteract}
                        ></button>
                    </div>
                </BrowserView>
            </div>
        </>
    );
}